.cv__toast {
  width: 100%;
  height: 100%;
  position: relative;
  border-radius: 5px;
  display: inline-flex;
  background: url("/assets/img/bg-button.png") repeat-x white;

  &--success {
    border: 1px solid #1cde93;
  }

  &--info {
    border: 1px solid #f8a700;
  }

  &--error {
    border: none;
  }

  &--message {
    display: flex;
    flex-direction: column;
    justify-content: center;
    font-family: "Roboto", sans-serif;
    font-size: 17px;
    letter-spacing: 1px;
    color: #221e1f;
    padding-left: 66px;
  }

  .icon {
    width: 40px;
    height: 40px;
    top: 50%;
    transform: translateY(-50%);
    left: 10px;
    position: absolute;

    &--success {
      background: url("/assets/svg/Success.svg") no-repeat;
    }

    &--info {
      background: url("/assets/svg/Info.svg") no-repeat;
    }

    &--error {
      background: url("/assets/svg/Error.svg") no-repeat;
    }
  }
}
