.profile__wrapper {
  .ant-card-head-title {
    font-weight: 600;
  }

  .ant-btn,
  .ant-upload {
    width: 100%;
  }

  .ant-btn {
    padding-top: 10px;
    padding-bottom: 10px;
    height: auto;
  }
}

.loading-cv {
  position: fixed;
  width: 100vw;
  height: 100vh;
  top: 0;
  left: 0;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background-color: rgba($color: black, $alpha: 0.2);
  z-index: 10000;
}
